
@import 'tailwindcss/base';
@import 'tailwindcss/components';
@import 'tailwindcss/utilities';

/*nav*/
@media (min-width: 775px) {
  #Nav_bar{
      position:absolute;
      width:fit-content;
      align-items: center;
      font-size: large;
      z-index:2;
      margin-top:3%;
      
    
  
  }
  .profile{
    width: fit-content;
    margin-left:2%;
    
  }
#link-icon img{
  
  margin-right: 1rem;
}

  



}


@media (max-width: 775px) {
  #Nav_bar{
      z-index:2;
      position:absolute;
      width:fit-content;
      align-items: center;
      font-size: medium;
      margin-top:6%;
      
      
      
  
  }
  .my_block{
    
    margin-left:5%;
  }
  .profile{
    margin-top:4%;
    margin-left:4%;
    height:10rem;
    width:20rem;
  }
#link-icon{
  height:3rem;
  
  }
#link-icon img{
  
}
  
}

















/*body*/
@media (min-width: 768px) {
  .profile{
    
    position:absolute;
    align-items: center;
    padding-top:8%;
  }
  .my_img{
    height:14rem;

  }

  #link-icon{
    height:3rem;
    
  }
  #link-icon spam:hover{
    height: 2.5rem;
    width: 2.5rem;
  }
  #link-icon spam img:hover{
    height: 2.5rem;
    width: 2.5rem;
  }
  
  
  
}


@media (max-Width:768px) {
  .profile{
    
    position:fixed;
    align-items: center;
    padding-top:20%;
    
    
  }
  .my_img{
    height:14rem;

  }
  #link-icon{
    height:3rem;
    
  }
  #link-icon spam:hover{
    height: 2.5rem;
    width: 2.5rem;
  }
  #link-icon spam img:hover{
    height: 2.5rem;
    width: 2.5rem;
  }
  
}
